   /* ControllerTesterStyles.css */


   body, html {
    margin: 0;
    overflow: hidden; 
    background-image: url(../images/InputBackground.png);
  }
  
  
  .gamepad-input-screen {
    width: 100%;
    height: 100%;
    padding: 50px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  
    transform-origin: center;
    color: white; 
    font-size: calc(10px + 2vmin);
    min-height: 100vh;
    position: relative; 
  }
  
  
  .content-left {
    width: 10%;
    height: auto; 
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color: white; 
  
    position: absolute; 
    top: 200px; 
    left: 10px; 
  }
  
  .content-right {
    color: white; 
    font-size: calc(15px + 2vmin);
    position: absolute; 
    top: 500px; 
    right: 150px; 
   
  }
  
  .qcfGIF {
    width: 10%; /* Adjust the width value as needed to make the image smaller */
    height: auto;
    position: absolute;
    top: 100px;
    right: 380px;
  }
  
  
  .default-gif {
    position: absolute;
    top: 10;
    left: 2;
    width: 10%;
    height: 10%;
    background-size: cover;
    z-index: 1;
  }
  
  .hidden {
    display: none;
  }

  .qcf {
    position: absolute;
    top: 1px;
    left: 90px; /* Adjust the left value as needed */
  }

  .otherimage {
    position: absolute;
    top: 90px;
    left: 90px; /* Adjust the left value as needed */
  }
  
  .gif-container {
    position: absolute;
    top: 10;
    left: 2;
    background-size: cover;
    z-index: 10;
    
  }
